import { Card, Col, Row, Table, Typography } from "antd";
import React from "react";
import DataTable from "react-data-table-component";
import { BsBuilding, BsTruck } from "react-icons/bs";
import { HiOutlineUserGroup } from "react-icons/hi";

import "./tabla.css";

const TablaHistorial = ({ columns, table, filas }) => {
	const columnsGeneral = columns.filter(
		(item) =>
			item.id === "Nro" ||
			item.id === "fecha_pago" ||
			item.id === "observacion" ||
			item.id === "pago_id" ||
			item.id === "nombre" ||
			item.id === "tipo" ||
			item.id === "volquetes" ||
			item.id === "estado" ||
			item.id === "Acciones"
	);

	const columnsExpandable = columns.map((item) => {
		return {
			title: item.name,
			dataIndex: item.id,
			key: item.id,
		};
	});

	const paginationComponentOptions = {
		rowsPerPageText: "Filas por página",
		rangeSeparatorText: "de",
		selectAllRowsItem: false,
		selectAllRowsItemText: "Todos",
	};

	const ExpandedComponent = ({ data }) => (
		<Row
			style={{ padding: 10, backgroundColor: "#F0F2F5" }}
			gutter={10}
			align="center"
		>
			{data.tipo === "asociacion" && (
				<Col lg={22} xs={24}>
					<Card>
						<strong>Asociación:</strong> {data.pagos?.nombre} <br />
						<strong>Tipo:</strong> {data.pagos?.tipo_asociacion}{" "}
						<br />
					</Card>
				</Col>
			)}
			<Col lg={11} xs={24}>
				<Card
					title={
						<>
							{data.tipo !== "casa" ? (
								<HiOutlineUserGroup />
							) : (
								<BsBuilding />
							)}
							{data.tipo !== "casa" ? "Trabajador/es" : "Empresa"}
						</>
					}
				>
					<Table
						style={{ width: "100%" }}
						size="small"
						pagination={false}
						columns={
							data.tipo === "casa"
								? columnsExpandable.filter(
										(item) => item.key === "empresa"
								  )
								: data.tipo === "asociacion"
								? columnsExpandable.filter(
										(item) =>
											item.key === "nombres" ||
											// item.key === "cargo" ||
											// item.key === "area" ||
											item.key === "dni" ||
											item.key === "teletrans" ||
											item.key === "telefono"
										// item.key === "inicio" ||
										// item.key === "fin"
								  )
								: columnsExpandable.filter(
										(item) =>
											item.key === "nombres" ||
											item.key === "cargo" ||
											item.key === "area" ||
											item.key === "dni" ||
											item.key === "telefono"
								  )
						}
						dataSource={
							data.tipo === "asociacion"
								? data.pagos?.trabajadores?.map(
										(item, index) => {
											return {
												key: index,
												nombres: (
													<Typography.Text
														style={{
															color: "#1890ff",
														}}
													>
														{item.nombre}
													</Typography.Text>
												),
												dni: item.dni,
												telefono: item.telefono,
												teletrans: item.teletrans,
											};
										}
								  )
								: data.tipo === "casa"
								? data.pagos?.trabajadores?.map(
										(item, index) => {
											return {
												key: index,
												empresa: item.nombre,
											};
										}
								  )
								: data.pagos?.trabajadores?.map(
										(item, index) => {
											return {
												key: index,
												nombres: (
													<Typography.Text
														style={{
															color: "#1890ff",
														}}
													>
														{item.nombre}
													</Typography.Text>
												),
												cargo: item.cargo,
												area: item.area,
												dni: item.dni,
												telefono: item.telefono,
											};
										}
								  )
						}
					/>
				</Card>
			</Col>
			<Col lg={11} xs={24}>
				<Card
					title={
						<>
							<BsTruck /> Destino/s
						</>
					}
				>
					<Table
						style={{ width: "100%" }}
						size="small"
						pagination={false}
						columns={columnsExpandable.filter(
							(item) =>
								item.key === "hora" ||
								item.key === "placa" ||
								item.key === "propietario" ||
								item.key === "trapiche" ||
								item.key === "teletrans"
						)}
						dataSource={data.destino.map((item, index) => {
							return {
								key: index,
								Nro: index + 1,
								hora: item.destino.hora,
								placa: item.destino.placa,
								propietario: item.destino.propietario,
								trapiche: item.destino.trapiche,
								teletrans: item.destino.teletrans,
							};
						})}
					/>
				</Card>
			</Col>
		</Row>
	);

	return (
		<div className="table-container">
			<DataTable
				size="small"
				columns={columnsGeneral}
				data={table}
				pagination
				striped
				highlightOnHover
				responsive
				paginationComponentOptions={paginationComponentOptions}
				paginationPerPage={filas || 8}
				paginationRowsPerPageOptions={[8, 16, 24, 32, 40]}
				expandableRows
				expandableRowsComponent={ExpandedComponent}
				expandableRowDisabled={(row) => row.disabled}
			/>
		</div>
	);
};

export default TablaHistorial;
