import { Col, DatePicker, Empty, Row, Select, Space, Typography } from "antd";
import { useContext, useEffect, useState } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { historialLayout } from "../../../data/dataTable";
import { notificacion } from "../../../helpers/mensajes";
import Cargando from "../../cargando/Cargando";
import TablaHistorial from "../../tabla/TablaHistorial";
import { ModalReprogramar } from "./ModalReprogramar";

function Historial() {
	const { getData } = useContext(CrudContext);
	const { RangePicker } = DatePicker;

	const [cargando, setCargando] = useState(false);
	const [historial, setHistorial] = useState([]);
	const [filterHistorial, setFilterHistorial] = useState([]);

	const getHistorial = async () => {
		setCargando(true);
		const route = "pago/historial";
		const response = await getData(route);
		if (response) {
			const filterNull = response.data.filter((item) => item !== null);
			setHistorial(filterNull);
			setCargando(false);
		}
	};

	useEffect(() => {
		getHistorial();
	}, []);

	useEffect(() => {
		setFilterHistorial(historial);
	}, [historial]);

	const handleValidar = async (e) => {
		const route = "pago/validacion/" + e.destino[0].pago_id;
		const response = await getData(route);
		if (response) {
			notificacion(response.status, response.msg);
			getHistorial();
		}
	};

	const [modalReprogramar, setModalReprogramar] = useState(false);
	const [dataReprogramar, setDataReprogramar] = useState({});

	const handleOpenModalReprogramar = () => {
		setModalReprogramar(true);
	};

	const handleCloseModalReprogramar = () => {
		setModalReprogramar(false);
	};

	const handleReprogramar = (e) => {
		setDataReprogramar(e);
		handleOpenModalReprogramar();
	};

	const columns = historialLayout(handleValidar, handleReprogramar);

	//* FILTROS DE BUSQUEDA
	const [tipoPago, setTipoPago] = useState("todos");
	const [fechaRango, setFechaRango] = useState(null);

	const handleTipoPago = (e) => {
		setTipoPago(e);
	};

	useEffect(() => {
		if (fechaRango !== null) {
			const { fechaInicio, fechaFin } = fechaRango;
			if (tipoPago === "todos") {
				const filter = historial.filter(
					(item) =>
						item.fecha_pago >= fechaInicio &&
						item.fecha_pago <= fechaFin
				);
				setFilterHistorial(filter);
			} else {
				const filter = historial.filter(
					(item) =>
						item.fecha_pago >= fechaInicio &&
						item.fecha_pago <= fechaFin &&
						item.tipo === tipoPago
				);
				setFilterHistorial(filter);
			}
		} else {
			if (tipoPago === "todos") {
				setFilterHistorial(historial);
			} else {
				const filter = historial.filter(
					(item) => item.tipo === tipoPago
				);
				setFilterHistorial(filter);
			}
		}
	}, [tipoPago]);

	const options = [
		{ value: "todos", label: "Todos" },
		{ value: "casa", label: "Casa" },
		{ value: "incentivo", label: "Incentivo" },
		{ value: "asociacion", label: "Asociacion" },
	];

	const handleBuscarFechas = async (e) => {
		if (e === null) {
			setFechaRango(null);
			if (tipoPago === "todos") {
				setFilterHistorial(historial);
			} else {
				const filter = historial.filter(
					(item) => item.tipo === tipoPago
				);
				setFilterHistorial(filter);
			}
		} else {
			const fechaInicio = e[0].format("YYYY-MM-DD");
			const fechaFin = e[1].format("YYYY-MM-DD");
			setFechaRango({ fechaInicio, fechaFin });

			if (tipoPago === "todos") {
				const filter = historial.filter(
					(item) =>
						item.fecha_pago >= fechaInicio &&
						item.fecha_pago <= fechaFin
				);
				setFilterHistorial(filter);
			} else {
				const filter = historial.filter(
					(item) =>
						item.fecha_pago >= fechaInicio &&
						item.fecha_pago <= fechaFin &&
						item.tipo === tipoPago
				);
				setFilterHistorial(filter);
			}

			// const route = `pago/fechas?inicio=${fechaInicio}&fin=${fechaFin}`;
			// const response = await getData(route);

			// if (response) {
			// 	const filterNull = response.data.filter(
			// 		(item) => item !== null
			// 	);
			// 	setFilterHistorial(filterNull);
			// }
		}
	};

	return (
		<div>
			<Row style={{ width: "100%" }}>
				<Col span={6}>
					<Space
						direction="horizontal"
						style={{
							width: "100%",
							display: "flex",
						}}
					>
						<Typography>Filtrar Pago tipo:</Typography>
						<Select
							style={{ width: 150 }}
							placeholder="Tipo de pago"
							defaultValue={tipoPago}
							onChange={handleTipoPago}
							options={options}
						/>
					</Space>
				</Col>
				<Col span={18}>
					<Space
						direction="horizontal"
						style={{
							width: "100%",
							display: "flex",
						}}
					>
						<Typography>Buscar por fechas:</Typography>
						<RangePicker onChange={handleBuscarFechas} />
					</Space>
				</Col>
			</Row>
			{historial?.length > 0 ? (
				<TablaHistorial columns={columns} table={filterHistorial} />
			) : (
				<>
					{cargando ? (
						<Cargando />
					) : (
						<Empty
							image={Empty.PRESENTED_IMAGE_SIMPLE}
							description={
								<span>No hay registros para mostrar.</span>
							}
						/>
					)}
				</>
			)}
			{modalReprogramar && (
				<ModalReprogramar
					data={dataReprogramar}
					open={modalReprogramar}
					onClose={handleCloseModalReprogramar}
					actualizarTabla={getHistorial}
				/>
			)}
		</div>
	);
}
export default Historial;
